@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;500;700;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Pacifico&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cookie&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600;700;800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: "Open Sans", sans-serif;
}

.App {
  text-align: center;
}

h1 {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-weight: bolder;
  font-size: 72px;
  color: #fff;
}

h2 {
  font-family: "Cookie", cursive;
  color: #fff;
}

h3 {
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-size: 28px;
}

h4 {
  font-weight: bolder;
  font-size: 28px;
}

h5 {
  font-size: 16px;
  color: #fff;
}

h6 {
  margin: 20px 0;
  line-height: 1.25;
}

p {
  font-family: "Montserrat", sans-serif;
}

.card {
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.2), 0 6px 6px 0 rgba(0, 0, 0, 0.19);
}

/*------------------------------------------- Topbar Styling ------------------------------------------*/
.topbar {
  background: rgba(0, 0, 0, 0.8);
}

.topbar-sub-button {
  font-size: 25px;
  color: #fff;
}

/* Banner Styling */
.banner {
  position: relative;
  background: rgba(10, 29, 41, 0.8);
  height: 80vh;
}

#background-video {
  position: fixed;
  z-index: -1;
  left: 0;
  top: 0;
  min-width: 100%;
  min-height: 100%;
}

.overlay-container {
  height: 100%;
  position: fixed;
  background-color: rgb(0, 0, 0); /* Black fallback color */
  background-color: rgba(0, 0, 0, 0.9); /* Black w/opacity */
}

.banner-text {
  position: absolute;
  z-index: 1;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

/* Banner\Get Slot Button */
.bookSlot {
  padding: 10px 15px;
  border: none;
  background: #3eadcf;
  color: #fff;
}

.bookSlot:hover {
  background-color: #1ea7d1;
  box-shadow: 0 4px 4px 2px rgba(0, 0, 0, 0.2), 0 6px 12px 0 rgba(0, 0, 0, 0.19);
}

/* Main Container[Calender, Sidepanel] */
.main-container {
  /* margin-top: 40px; */
  background-image: linear-gradient(315deg, #3eadcf, #abe9cd);
  background-size: cover;
  padding: 100px 0;
}

/*------------------------------------------ Sidepanel Styling ----------------------------------------*/

.timings {
  display: grid;
  grid-template-columns: auto auto;
}

.available-slots {
  margin: 12px;
  font-size: 14px;
  padding: 14px 40px;
  border: 1px solid #fff;
  background: #fff;
  color: #3eadcf;
  transition: 0.25s;
}

.available-slots:hover {
  background-image: linear-gradient(295deg, #3eadcf, #7acdce);
  color: #fff;
  border: 1px solid #fff;
  padding: 14px 40px;
  box-shadow: 4px 4px 4px 0 rgba(0, 0, 0, 0.2), 0 6px 12px 0 rgba(0, 0, 0, 0.19);
}

.proceed {
  font-size: 14px;
  padding: 10px 15px;
  border: 1px solid #fff;
  font-size: 14px;
  color: #fff;
  background: transparent;
}

.proceed:hover {
  background: #3eadcf;
}

.slot-proceed {
  width: 100%;
}

.slot-proceed:hover {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.App-link {
  color: #61dafb;
}

/*------------------------------------------- Calender Styling ------------------------------------------*/
.calendar {
  margin: 0 auto;
  margin-right: 25px;
  width: 600px;
  background-color: #fff;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.month {
  width: 100%;
  padding: 40px 30px;
  text-align: center;
  background-color: #fff;
  color: #fff;
}

#month {
  font-size: 42px;
  margin: 0;
  font-family: "Cookie", cursive;
  background: -webkit-linear-gradient(#3eadcf, #7acdce);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-weight: 500;
}

.weekdays {
  background-color: #3eadcf;
  color: #fff;
  padding: 7px 0;
  display: flex;
}

.days {
  background-color: #fff;
  font-weight: 300;
  padding: 10px 0;
  display: flex;
  flex-wrap: wrap;
}

.weekdays div,
.days div {
  text-align: center;
  width: 14.28%;
}

.days CalenderCell {
  text-align: center;
  width: 14.28%;
}

.days div {
  padding: 10px 0;
  margin-bottom: 10px;
  transition: all 0.4s;
}

.prev_date {
  color: #999;
}

.today {
  border: 2px solid #3eadcf;
}

.selected-date {
  background-color: #3eadcf;
  color: #fff;
}

.disabled{
  color: #dfe6e9
}

.days div:hover {
  cursor: pointer;
  background-color: #6bbdd6;
  color: #fff;
}

.prev,
.next {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  font-size: 23px;
  background-color: #3eadcf;
  transition: all 0.4s;
}

.prev:hover,
.next:hover {
  cursor: pointer;
}

/* Form Styling */
.appointment-form {
  padding: 20px 40px 20px 20px;
  max-width: 500px;
  text-align: left;
  /* margin: 50px; */
}

.appointment-form input {
  font-family: "Open Sans", sans-serif;
  outline: 0;
  width: 90%;
  padding: 5px;
  font-size: 12px;
  border: none;
  border-bottom: 1px solid #8b8b8b;
  margin: 5px;
}

.appointment-form textarea {
  font-size: 12px;
  border: none;
  outline: none;
  border-bottom: 1px solid #8b8b8b;
  margin: 0 5px 5px 5px;
  padding: 5px;
}

.form-footer {
  display: flex;
  justify-content: center;
}

/*--------------------------------------- Youtube Container Styling --------------------------------------*/
.yt-container {
  text-align: left;
  padding: 100px 0;
  background-color: #212121;
  color: #fff;
  margin: auto;
}

.yt-logo {
  width: 250px;
}

.yt-container span {
  font-weight: 800;
}

.yt-container-text img {
  background: #fff;
  padding: 3px;
  border-radius: 30px;
  margin-right: 5px;
}

.yt-subs {
  font-size: 12px;
  border: none;
  padding: 4px;
  background-color: rgb(245, 19, 19);
  color: #fff;
}

.yt-subs a {
  color: #fff;
  text-decoration: none;
}

.promotion {
  background-color: #f5f5f7;
  padding: 20px 15px;
}

.promotion .promo-card {
  padding: 50px;
  background-color: #fff;
}

.promotion .plugsvector {
  width: 300px;
}

/* --------------------------------------------- Footer Styling --------------------------------------*/
.footer {
  padding: 38px 30px 10px 30px;
  text-align: left;
  background-color: #f5f5f7;
}

.footer h3 {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-size: 24px;
  font-weight: bolder;
  color: #535151;
}

.footer-brandName {
  text-align: left;
}

.footer-logo {
  width: 50px;
}

.footer p {
  font-size: 14px;
  font-weight: 500;
}

.footer-subscribe {
  width: 200px;
}

.social p {
  font-size: 12px;
}

.social span {
  font-weight: 700;
  color: #535151;
}

.social-icons {
  padding: 8px;
  color: #000;
  border-radius: 50%;
}

.social-icons:hover {
  color: #1ea7d1;
}

.paymentSuccess span {
  color: #1ed14b;
}

@media (max-width: 768px) {
  .yt-container-text {
    max-width: 80%;
    margin: auto;
  }

  .banner {
    background: #000;
  }

  #bakground-video {
    display: none;
    height: 0;
  }
}

